.button {
    font-family: "Lato", sans-serif;
    padding: 0.5rem 1.5rem;
    margin: 5px;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.26);
    cursor: pointer;
    background: whitesmoke;
    font-size: 15px;
    font-weight: bold;
    width: auto;
}

.button:hover,
.button:active {
    background: #ac0e77;
    border-color: #ac0e77;
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.26);
    color: whitesmoke;
}